@media screen and (max-width: 991px) {
        .logo {
            max-width: 126px;
        }
    
        .navbar {
            padding: 17px 0 15px;
        }
    
        .navbar-collapse {
            background-color: $bg-color-accent;
            margin: 0 -11px;
            padding: 0px 40px 0px;
            text-align: center;
            transform: translate(0, 12px);
        }
    
        .navbar-nav {
            padding: 37px 0 34px;
            + .btn {
                margin-bottom: 60px;
                width: 100%;
                max-width: 295px;
                font-size: 16px;
                padding: 21px 27px;
            }
    
            .nav-link {
                color: #fff;
                font-size: 24px;
                line-height: 222%;
                &:hover {
                    color: $primary-color;
                }
            }
        }
    }